import React, { Component } from 'react'
import Loading from '../Loading/loading'
import './notification.css'

class Notification extends Component {
  state = { loading: false, pdfUrl: null }

  async downloadResumeHandler(e) {
    e.preventDefault()
    try {
      this.setState({ loading: true })
      const response = await fetch(
        'https://5c2kf0lnal.execute-api.ap-southeast-2.amazonaws.com/Prod/generate-pdf/',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ url: 'https://gsswain.com/resume' }),
        }
      )
      const { pdfUrl } = await response.json()
      this.setState({ loading: false, pdfUrl })
      const returnValue = window.open(pdfUrl, '_blank')
      if (returnValue == null) {
        if (
          confirm(
            'A pop up blocker blocked the PDF from getting downloaded. Click Ok to open.'
          )
        ) {
          window.open(pdfUrl, '_blank')
        }
      }
    } catch (err) {
      alert('Error downloading resume')
    } finally {
      this.setState({ loading: false })
    }
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (!prevState.loading) {
      return { loading: false }
    }
    return null
  }

  render() {
    return (
      <section className="notification">
        <Loading loading={this.state.loading}></Loading>
        <p className="highlight-sm">
          I have started looking for work in Sydney, Australia. You can view my
          resume{' '}
          <a href="./resume" rel="noreferer" target="_blank">
            here
          </a>{' '}
          and download a PDF copy from{' '}
          {!this.state.pdfUrl && (
            <a href="#" onClick={this.downloadResumeHandler.bind(this)}>
              {' '}
              here{' '}
            </a>
          )}
          {this.state.pdfUrl && <a href={this.state.pdfUrl}> here</a>}
          .If you find my profile relevant for an opportunity, please drop me a{' '}
          <a
            href="mailto:contact@gsswain.com?subject=Work opportunity"
            name="Girija's email"
            title="Send a work opportunity related message to Girija"
          >
            mail
          </a>
          .
        </p>
      </section>
    )
  }
}
export default Notification
